@import '../node_modules/antd/dist/antd.css';

*{
    margin: 0;
}

.header-menu{
    display:flex;
    justify-content: flex-end;
}
.aboutMe{
    margin: 3rem;
    font-size: 1rem;

    &>section::after{
        content:"";
        border: 1px solid gray;
        display: inline-block;
        width: 100%;
    }
    
    h3, h4{
        font-weight:bolder;
    }
}
.principal{
    // position:fixed;
    height: 100%;
    width: 100%;
    
    // padding: 1rem;
    // border: 1px solid black;
    // box-shadow: 0.2rem 0.2rem 0.2rem gray;
    

    .title{
        font-weight: bold;
        font-size: 1.5rem;
    }
    .avatarContainer{
        display:flex;
        justify-content: center;
        .avatar{
            border: 5px gainsboro;
            border-style:ridge;
            margin: 0 auto;
        }
    }

}